@import "../../../styles/functions";
@import "../../../styles/mixins";
.Menu {
  height: 100%;
  padding: toRem(10) 0 toRem(20) 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--blue-500);
  color: var(--white);
  width: 5rem;

  @include base-transition;
  &_open {
    width: calc(247 / 1200 * 100%);
    padding: toRem(40) 0 toRem(20) 0;
  }

  &-ButtonContainer {
    margin-top: auto;
    width: 100%;
    padding: 0 toRem(20);
  }

  &-Button {
    padding: toRem(3.5) 0;
    width: 100%;
    background-color: var(--blue-100);
  }
}

@media only screen and (max-width: 900px) {
  .Menu {
    &_open {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 2;
      width: 100%;
      overflow: hidden;
    }
  }
}
