:root {
  --font-family: Roboto, sans-serif;

  //xxs font
  --font-size-xxs: 8px;
  //xsmall font
  --font-size-xs: 12px;

  //small font
  --font-size-s: 14px;

  //medium font
  --font-line-m: 20px;
  --font-size-m: 16px;

  //large font
  --font-line-l: 21px;
  --font-size-l: 18px;

  //xlarge font
  --font-size-xl: 24px;

  //font weights
  --font-weight-regular: 400;
  --font-weight-bold: 700;

  //line-height
  --line-height: 20px;
}
